.logo {
  width: $logo-width;
  height: $logo-heigth;

  @media (max-width: $small-screen) {
    width: $small-logo-width;
    height: $small-logo-heigth;
  }

  background-image: url(../images/logo.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
